<template>
  <div id="user">
    <div class="breadcrumb-box">
      <Breadcrumb>
        <Breadcrumb-item href="/home"> 首页 </Breadcrumb-item>
        <Breadcrumb-item> 人员管理 </Breadcrumb-item>
      </Breadcrumb>
    </div>
    <Row>
      <Card class="table-box">
        <Row>
          <i-col span="3">
            <i-button type="primary" @click.native="showNew" style="float: left;">
              <Icon type="plus-round"></Icon>
              添加用户
            </i-button>
          </i-col>
          <i-col span="21">
            <i-form :model="formInline" inline style="float: right;">
              <Radio-group v-model="formInline.field" type="button">
                <Radio label="id">用户编号</Radio>
                <Radio label="username">登录名</Radio>
                <Radio label="alias">别称</Radio>
              </Radio-group>
              <Form-item>
                <i-input type="text" v-model="formInline.value" placeholder="可对UID、用户名、昵称进行搜索"></i-input>
              </Form-item>
              <Form-item>
                <i-select v-model="formInline.group_id" style="width: 100px;">
                  <i-option :value="0">全部分组</i-option>
                  <i-option v-for="item in groupList" :value="item.id">{{ item.name }}</i-option>
                </i-select>
              </Form-item>
              <Form-item>
                <i-select v-model="formInline.status" style="width: 100px;">
                  <i-option v-for="item in statusList" :value="item.status">{{ item.name }}</i-option>
                </i-select>
              </Form-item>
              <Form-item>
                <i-button type="success" @click.native="handleSubmit()"> 搜索 </i-button>
              </Form-item>
            </i-form>
          </i-col>
        </Row>
        <Row>
          <i-col spna="24">
            <div class="ivu-table-wrapper">
              <div class="ivu-table ivu-table-border ivu-table-stripe">
                <table cellspacing="0" cellpadding="0" border="0" style="width: 100%;">
                  <thead class="ivu-table-header">
                  <tr class="ivu-table-row">
                    <th class="ivu-table-column-center" width="10%"><div class="ivu-table-cell"><span>编号</span></div></th>
                    <th class="ivu-table-column-center" width="20%"><div class="ivu-table-cell"><span>用户名</span></div></th>
                    <th class="ivu-table-column-center" width="15%"><div class="ivu-table-cell"><span>别称</span></div></th>
                    <th class="ivu-table-column-center" width="20%"><div class="ivu-table-cell"><span>注册时间</span></div></th>
                    <th class="ivu-table-column-center" width="10%"><div class="ivu-table-cell"><span>用户组</span></div></th>
                    <th class="ivu-table-column-center" width="10%"><div class="ivu-table-cell"><span>状态</span></div></th>
                    <th class="ivu-table-column-center" width="15%"><div class="ivu-table-cell"><span>操作</span></div></th>
                  </tr>
                  </thead>
                  <tbody class="ivu-table-tbody">
                  <tr class="ivu-table-row" v-for="(item, index) in list">
                    <td class="ivu-table-column-center"><div class="ivu-table-cell"><span>{{ item.id }}</span></div></td>
                    <td class=""><div class="ivu-table-cell"><div>{{ item.username }}</div></div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell"><span>{{ item.alias }}</span></div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell"><span>{{ getTime(item.create_at) }}</span></div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ getGroupName(item.group_id) }}</div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">
                      <span class="text-success" v-if="parseInt(item.status) === 1">正常</span>
                      <span class="text-danger" v-else-if="parseInt(item.status) === 0">锁定</span>
                      <span class="text-grey" v-else>删除</span>
                    </div></td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">
                        <div>
                          <button type="button" class="ivu-btn ivu-btn-info ivu-btn-small" @click="showEdit(index)"><span>修改</span></button>
                          <button type="button" class="ivu-btn ivu-btn-error ivu-btn-small" @click="showDelete(index)"><span v-if="item.status === 2">清空</span><span v-else>删除</span></button>
                        </div>
                      </div>
                    </td>
                  </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </i-col>
        </Row>
        <div class="table-page">
          <Page :total="total" show-elevator @on-change="getList"></Page>
        </div>
      </Card>
    </Row>

    <Modal
      title="用户编辑"
      v-model="userPanel"
      :mask-closable="false"
      :loading="true"
      @on-ok="setUser"
      width="720">
      <Row>
        <i-col span="20" offset="2">
          <i-form v-model="handleUser" :label-width="100">
            <Form-item label="用户编号">
              <i-input v-model="handleUser.id" disabled></i-input>
            </Form-item>
            <Form-item label="登录名">
              <i-input v-if="handleUser.id === 0" v-model="handleUser.username"></i-input>
              <i-input v-else v-model="handleUser.username" disabled></i-input>
            </Form-item>
            <Form-item label="用户状态">
              <Radio-group v-model="handleUser.gender" type="button" size="small" style="float: left;">
                <Radio label="0">保密</Radio>
                <Radio label="1">男士</Radio>
                <Radio label="2">女士</Radio>
              </Radio-group>
            </Form-item>
            <Form-item label="别称">
              <i-input v-model="handleUser.alias" placeholder="不填写则不修改"></i-input>
            </Form-item>
            <Form-item label="重置密码">
              <i-input v-model="handleUser.password" placeholder="不填写则不修改"></i-input>
            </Form-item>
            <Form-item label="用户组变更">
              <i-select v-model="handleUser.group_id">
                <i-option v-for="item in groupList" :value="item.id">{{ item.name }}</i-option>
              </i-select>
            </Form-item>
            <Form-item label="用户状态">
              <Radio-group v-model="handleUser.status" type="button" size="small" style="float: left;">
                <Radio label="1">正常</Radio>
                <Radio label="0">锁定</Radio>
                <Radio label="2" v-show="parseInt(handleUser.status) === 2">已删除</Radio>
              </Radio-group>
            </Form-item>
          </i-form>
        </i-col>
      </Row>
    </Modal>
  </div>
</template>

<script>
  import moment from 'moment'
  import md5 from 'md5/md5.js'

  export default {
    data () {
      return {
        self: this,
        userPanel: false,
        total: 1,
        formInline: {
          field: 'id',
          value: '',
          group_id: 0,
          status: 1
        },
        handleUser: {},
        groupList: [],
        statusList: [
          { status: -1, name: '所有状态' },
          { status: 0, name: '锁定' },
          { status: 1, name: '正常' },
          { status: 2, name: '删除' }
        ],
        list: []
      }
    },
    created () {
      this.initList()
    },
    watch: {
      '$route': 'initList'
    },
    methods: {
      handleSubmit () {
        this.$http.post('/admin_api/users', this.formInline).then(response => {
          if (response.body.error === 0) {
            this.list = response.body.data.data
          } else {
            this.$Notice.error({ 'title': '查询错误', 'desc': response.body.message })
          }
        })
      },
      showNew () {
        this.handleUser = {
          id: 0,
          username: '',
          gender: 0,
          alias: '',
          group_id: 0,
          password: '',
          status: 1
        }
        this.userPanel = true
      },
      showEdit (index) {
        this.handleUser = this.list[index]
        this.handleUser.password = ''
        this.userPanel = true
      },
      showDelete (index) {
        this.$Modal.confirm({
          title: '确定删除?',
          content: '那就删除吧~',
          loading: true,
          onOk: () => {
            this.$http.post('/admin_api/users/delete', { id: this.list[index].id }).then(response => {
              if (response.body.error === 0) {
                this.list[index] = {}
                this.$Message.success(response.body.message)
                this.$Modal.remove()
              } else {
                this.$Notice.error({ 'title': '请求失败', 'desc': response.body.message })
              }
            })
          },
          cancelText: '取消'
        })
      },
      getList (page) {
        this.$http.post('/admin_api/users?page=' + page, this.formInline).then(response => {
          if (response.body.error === 0) {
            this.list = response.body.data.data
            this.groupList = response.body.data.group_list
            this.total = response.body.data.total
          } else {
            this.$Notice.error({ 'title': '获取数据失败', 'desc': response.body.message })
          }
        })
      },
      setUser () {
        // 异步修改用户信息
        let url = '/admin_api/users/new'
        if (this.handleUser.id !== 0) {
          url = '/admin_api/users/edit'
        }
        let userData = this.handleUser
        console.log(typeof (this.handleUser.password))
        if (this.handleUser.password !== '') {
          userData.password = md5(this.handleUser.password)
        }
        this.$http.post(url, userData).then(response => {
          if (response.body.error === 0) {
            this.$Message.success('对 ' + this.handleUser.username + ' 用户的操作以完成')
            if (userData.id === 0) {
              this.handleUser = {}
            } else {
              let self = this
              this.list.map(function (item, index) {
                if (item.id === userData.id) {
                  self.list[index].gender = parseInt(userData.gender)
                  self.list[index].group_id = parseInt(userData.group_id)
                  self.list[index].status = parseInt(userData.status)
                }
              })
            }
            this.$Modal.remove()
          } else {
            this.$Notice.error({ 'title': '请求失败', 'desc': response.body.message })
            this.$Modal.loading = false
          }
        })
      },
      getGroupName (gid) {
        for (let index = 0; index < this.groupList.length; index++) {
          if (gid === this.groupList[index].id) {
            return this.groupList[index].name
          }
        }
        return '无分组'
      },
      getTime (date) {
        return moment.unix(date).format('YYYY-MM-DD h:mm')
      },
      initList () {
        this.$http.post('/admin_api/users', this.formInline).then(response => {
          if (response.body.error === 0) {
            this.list = response.body.data.data
            this.groupList = response.body.data.group_list
            this.total = response.body.data.total
          } else {
            this.$Notice.error({ 'title': '获取数据失败', 'desc': response.body.message })
          }
        })
      }
    }
  }
</script>

<style>
  .table-box {
    width: 98%;
    margin: 10px 1%;
    position: relative;
    top: 0;
    bottom: 0;
    overflow: hidden;
  }
  .table-page {
    display: block;
    float: right;
    margin: 15px 10px;
  }
</style>



